import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, platform, HeaderButton, IOS} from '@vkontakte/vkui';
import connect from '@vkontakte/vkui-connect';

import '@vkontakte/vkui/dist/vkui.css';

import books from '../books/books.json';
import vkuiConnect from '@vkontakte/vkui-connect';

import Icon24Document from '@vkontakte/icons/dist/24/document';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24ShareOutline from '@vkontakte/icons/dist/24/share_outline';

import './books.css';


class BookDetail extends React.Component {
	constructor (props) {
		super(props);

		this.state = {
			bookText: null,
			activeStory: "home",
			check: false,
			activePanel: 'feed',
      history: ['feed'],
			page: this.props.bookStartId
		};
	}
	
	goBack = () => {
		const history = [...this.state.history];
		history.pop();
		const activePanel = history[history.length - 1];
		if (activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppDisableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  goForward = (activePanel) => {
		const history = [...this.state.history];
		history.push(activePanel);
		if (this.state.activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppEnableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  go = (e) => { 
		this.setState({ activePanel: e.currentTarget.dataset.to }) 
		};

	getBookText = (id,glava) => {
		console.log(require("../books"+books[id].preview))
		fetch(require("../books/content"+require("../books"+books[id].preview)[glava].text))
		.then(text => text.text())
		.then(data => {this.setState({bookText: data})})
		this.setState({page: glava, check: true})
		let status = window.navigator.onLine ? true : false;
if(!status) {alert("Проверьте интернет соединение.\nПовторите попытку");}
	}
	
	
	render () {
		const osname = platform();
		return (
			<Panel id={this.props.id}>
				<PanelHeader
			left={<HeaderButton onClick={this.props.go} data-to="feed">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}>
		</PanelHeader>
									<img alt={books[this.props.bookId].title} className="img" type="image" src={require("../books"+books[this.props.bookId].path)}/> 
						<Group className="videl" title="Информация о книге:">
						<Div>
                <span className="videl" className="info">Жанры: </span><span className="nackl" className="videl" className="noperenos"> {books[this.props.bookId].genreall}</span>
						   <br></br>
						   <br></br>
						   <span className="videl" className="info">Дата написания: </span><span className="videl" className="nackl" className="noperenos"> {books[this.props.bookId].year}</span>
						   <br></br>
						   <br></br>
							 <span className="videl" className="info">Страна написания: </span><span className="videl" className="nackl" className="noperenos"> {books[this.props.bookId].country}</span>
						   <br></br>
               <br></br>
						   <span className="videl" className="info">Примерное время прочтения: </span> <span className="videl" className="nackl" className="noperenos"> {books[this.props.bookId].time}</span>
						   <br></br>
						   <br></br>
						   </Div>
						</Group>
				<Group>
					<Div>
					{books[this.props.bookId].text}
					</Div>
				</Group>
			</Panel>
        );
    }
}
BookDetail.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


export default (BookDetail);

