import React from 'react';
import { Epic, Panel, Tabbar, Alert, TabbarItem, PanelHeader, View, Group, HorizontalScroll, Tappable, Search, List, Cell, Button} from '@vkontakte/vkui';

import BookDetail from './BookDetail';
import PropTypes from 'prop-types';

import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon24Search from '@vkontakte/icons/dist/24/search';
import Icon28Message from '@vkontakte/icons/dist/28/message';

import './books.css';
import books from '../books/books.json';

import skoro1 from '../books/img/skoro1.jpg';
import skoro2 from '../books/img/skoro2.jpg';
import skoro3 from '../books/img/skoro3.jpg';
import skoro4 from '../books/img/skoro4.jpg';
import skoro5 from '../books/img/skoro5.jpg';
import skoro6 from '../books/img/skoro6.jpg';


const book = Object.keys(books).map(i => {
	return books[i]
})


class Home  extends React.Component {
	constructor (props,) {
	  super(props);
  
		this.state = {
			activeStory: 'feed',
			bookId: null,
			popout: null,
			search: '',
			contextOpened: true,
            mode: 'all'
		};
		this.openDefault = this.openDefault.bind(this);
    this.openDestructive = this.openDestructive.bind(this);
    this.closePopout = this.closePopout.bind(this);
		this.onChange = this.onChange.bind(this);
		this.onStoryChange = this.onStoryChange.bind(this);
	  this.toggleContext = this.toggleContext.bind(this);
    this.select = this.select.bind(this);
  }
	 
	
	componentDidMount() {
    this.openDestructive()
	}
	
	openDefault () {
    this.setState({ popout:
      <Alert
        actions={[{
          title: 'Отмена',
          autoclose: true,
          style: 'cancel'
        }, {
          title: 'Добавить',
          autoclose: true,
        }]}
        onClose={this.closePopout}
      >
        <h2>Подтвердите действие</h2>
        <p>Добавить пользователю право на модерацию контента.</p>
      </Alert>
    });
  }

  openDestructive () {
    this.setState({ popout:
      <Alert
        actionsLayout="vertical"
        actions={[{
          title: 'Лишить права',
          autoclose: true,
          style: 'destructive'
        }, {
          title: 'Отмена',
          autoclose: true,
          style: 'cancel'
        }]}
        onClose={this.closePopout}
      >
        <h2>Подтвердите действие</h2>
        <p>Вы уверены, что хотите лишить пользователя права на модерацию контента?</p>
      </Alert>
    });
  }

  closePopout () {
    this.setState({ popout: null });
  }

	toggleContext () {
		this.setState({ contextOpened: !this.state.contextOpened });
	  }
	
	select (e) {
		const mode = e.currentTarget.dataset.mode;
		this.setState({ mode });
		requestAnimationFrame(this.toggleContext);
	  }
	

	onChange (search) { this.setState({ search }); }
	
	get book () { 
		const search = this.state.search.toLowerCase();
		return book.filter(({forpoisk}) => forpoisk.toLowerCase().indexOf(search) > -1);
	}

	
	go = (e) => { 
		this.setState({ activeStory: e.currentTarget.dataset.to }) 
		};

	onStoryChange (e) {
		this.setState({ activeStory: e.currentTarget.dataset.story })
		window.scrollTo(0,0)
	}

	selectBook = id => {
		this.setState({ bookId: id, activeStory: "bookDetail"})
		window.scrollTo(0,0)
	}
	
	render () {
		return (
			<Epic activeStory={this.state.activeStory} tabbar={
				this.state.activeStory !== 'bookDetail' &&
			<Tabbar>
			<TabbarItem
				onClick={this.onStoryChange}
				selected={this.state.activeStory === 'feed'}
				data-story="feed"
				text="Каталог"
			><Icon24List/></TabbarItem>
			<TabbarItem
				onClick={this.onStoryChange}
				selected={this.state.activeStory === 'discover'}
				data-story="discover"
				text="Поиск"
			><Icon24Search/></TabbarItem>
			</Tabbar>
		}>
			<View id="feed" activePanel="feed" >
			<Panel id="feed">
				<PanelHeader>Каталог</PanelHeader>
				<Group className="videl" title="Скоро"> 
			<HorizontalScroll style={{padding: '0px 2px'}}>
				<div style={{ display: 'flex' }}> 
				<Tappable style={{borderRadius: 15, margin: 2}}>
		<div style={{ padding: 5, width: 96 }}> 
		<div><img className="ico-top" alt={1} type="image" src={skoro1} /></div> 
		<div className="virav" style={{paddingTop: 10}}>Невеста</div> 
		</div> 
		</Tappable> 
	    	<Tappable style={{borderRadius: 15, margin: 2}}>
		<div style={{ padding: 5, width: 96 }}> 
		<div><img className="ico-top" alt={1} type="image" src={skoro2} /></div> 
		<div className="virav" style={{paddingTop: 10}}>Жених</div> 
		</div> 
		</Tappable> 
		           <Tappable style={{borderRadius: 15, margin: 2}}>
		<div style={{ padding: 5, width: 96 }}> 
		<div><img className="ico-top" alt={1} type="image" src={skoro3} /></div> 
		<div className="virav" style={{paddingTop: 10}}>После бала</div> 
		</div> 
		</Tappable> 
		           <Tappable style={{borderRadius: 15, margin: 2}}>
		<div style={{ padding: 5, width: 96 }}> 
		<div><img className="ico-top" alt={1} type="image" src={skoro4} /></div> 
		<div className="virav" style={{paddingTop: 10}}>Нос</div> 
		</div> 
		</Tappable> 
		           <Tappable style={{borderRadius: 15, margin: 2}}>
		<div style={{ padding: 5, width: 96 }}> 
		<div><img className="ico-top" alt={1} type="image" src={skoro5} /></div> 
		<div className="virav" style={{paddingTop: 10}}>Три смерти</div> 
		</div> 
		</Tappable>
		            <Tappable style={{borderRadius: 15, margin: 2}}>
		<div style={{ padding: 5, width: 96 }}> 
		<div><img className="ico-top" alt={1} type="image" src={skoro6} /></div> 
		<div className="virav" style={{paddingTop: 10}}>Левша</div> 
		</div> 
		</Tappable> 
				</div> 
				<br/> 
			</HorizontalScroll> 
		</Group>
				<Group className="videl" title="Каталог книг" description={<div style={{textAlign: "center"}}>Не нашли, что искали? Ждите обновлений!</div>}>
					<List>
						{
							Object.keys(books).map(i => <Cell
								onClick={() => this.selectBook(i)}
								key={i}
								before={<div className="ico-align"><img alt={books[i].title} className="ico-crop" type="image" src={require("../books"+books[i].path)} /><br/></div>}
							>
							<span>{books[i].title}</span>
							<span className="disable-selection text">Автор: {books[i].author} </span>
							<span className="disable-selectionn text">Жанр: {books[i].genre}</span>
							</Cell>)
						}
					</List> 
				</Group>
				<Button before={<Icon28Message/>} size="xl" level="secondary" component="a" href="https://vk.com/onlyvkbook" target="_blank"> Сообщество</Button>
					<br></br>
			</Panel>
			</View>

			<View id="discover" activePanel="discover">
			<Panel id="discover">
			<PanelHeader>Поиск</PanelHeader>
				<Search value={this.state.search} onChange={this.onChange}/>
				<List>
					{
this.book.map((book,r) => ( 
<Cell 
key={r+1} 
onClick={() => this.selectBook(r+1)} 
>{book.author} - {book.title}</Cell> 
)).sort((a,b) =>{ return a-b })
}
				</List>
			</Panel>
			</View>

			<View id="bookDetail" activePanel="bookDetail">
				<BookDetail id="bookDetail" go={this.go} bookId={this.state.bookId} bookStartId={1} />
			</View>
		</Epic>
	)}
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;